<script>
import HomeMobile from './HomeMobile'
export default {
    components: {
        'HomeMobile': HomeMobile,
    },
    data() {
        return {
            background_height: [],
            background_width: [],
            anchor_isActive: [],
        }
    },
    created() {
        window.addEventListener('mousewheel', this.getSectionY, {
            passive: false
        });
    },
    mounted:function() {
        for (let index = 0; index < 5; index++) {
            this.anchor_isActive[index] = false;
        }
        var window_width = window.innerHeight;
        // console.log(window_width);
        this.$set(this.background_height, 0, window_width);
        var window_height = window_width * 3;
        this.$set(this.background_height, 0, window_height);
    },
    methods: {
        anchorClick(i) {
            for (let index = 0; index < 5; index++) {
                if (index == i ) {
                    this.anchor_isActive[index] = true;
                    this.$set(this.anchor_isActive, index, true);
                    console.log("anchor is clicked",this.anchor_isActive[index])
                }else {
                    this.anchor_isActive[index] = false;
                }
            }
        },
        getSectionY() {
            var y_coordinate = window.pageYOffset;
            // console.log(y_coordinate);
            if (y_coordinate > 0 && y_coordinate < 500) {
                for (let index = 0; index < 5; index++) {
                    this.anchor_isActive[index] = false;
                }
                this.$set(this.anchor_isActive, 0, true);
            }
            if (y_coordinate > 1000 && y_coordinate < 1500) {
                for (let index = 0; index < 5; index++) {
                    this.anchor_isActive[index] = false;
                }
                this.$set(this.anchor_isActive, 1, true);
            }
            if (y_coordinate > 2000 && y_coordinate < 2500) {
                for (let index = 0; index < 5; index++) {
                    this.anchor_isActive[index] = false;
                }
                this.$set(this.anchor_isActive, 2, true);
            }
            if (y_coordinate > 5000 && y_coordinate < 5500) {
                for (let index = 0; index < 5; index++) {
                    this.anchor_isActive[index] = false;
                }
                this.$set(this.anchor_isActive, 3, true);
            }
            if (y_coordinate > 6000 && y_coordinate < 6500) {
                for (let index = 0; index < 5; index++) {
                    this.anchor_isActive[index] = false;
                }
                this.$set(this.anchor_isActive, 4, true);
            }
        }
    },
};
</script>
